import React, { useEffect, useState } from 'react';
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import ListItemIcon from "@mui/material/ListItemIcon";
import Divider from "@mui/material/Divider";
import Skeleton from "@mui/material/Skeleton";
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';

function MoneyControl() {
  const [moneyNews, setNews] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch(`/scrape`)
      .then((response) => response.json())
      .then((data) => {
        setNews(data);
        setLoading(false);
      })
      .catch((error) => console.error("Error:", error));
  }, []);

  return (
    <>
      <List>
        {loading ? (
          Array.from({ length: 5 }).map((_, index) => (
            <React.Fragment key={`skeleton-${index}`}>
              <ListItem>
                <ListItemIcon>
                  <AccountBalanceIcon />
                </ListItemIcon>
                <ListItemText>
                  <Skeleton animation="wave" height={30} />
                </ListItemText>
              </ListItem>
              {index !== 4 && <Divider orientation="horizontal" flexItem />}
            </React.Fragment>
          ))
        ) : (
          moneyNews.slice(0, 6).map((entry, index) => ( // Display only the first 6 entries
            <React.Fragment key={`item-${index}`}>
              <ListItem button component="a" href={entry.link} target="_blank" rel="noopener noreferrer">
                <img
                  src="/blog/0.png"
                  alt="Smartvitta"
                  style={{
                    display: { xs: 'none', md: 'flex' },
                    marginRight: '1rem',
                    width: '42px',
                    height: '42px',
                    backgroundColor: '#343C4F',
                    borderRadius: '50%',
                    padding: '4px',
                  }}
                />
                <ListItemText primary={entry.headline} primaryTypographyProps={{ style: { fontSize: '14px' } }} />
              </ListItem>
              {index !== 5 && <Divider orientation="horizontal" flexItem />} {/* Only show divider for the first 5 cards */}
              <Divider orientation="horizontal" flexItem style={{ backgroundColor: '#343C4F' }} />
            </React.Fragment>
          ))
        )}
      </List>
    </>
  );
}

export default MoneyControl;
